var fullScreen = new FullScreen()

//var resizeEvent = window.document.createEvent('UIEvents') 
//resizeEvent .initUIEvent('resize', true, false, window, 0)

function onClick(id, callback) {
  document.getElementById(id).addEventListener('click', callback)
}

window.addEventListener("load", function(event) {

}, false)

//window.addEventListener("resize", function(event) {
//  //mapContainer.style.height = window.innerHeight + 'px'
//})


onClick('full-screen', function() {
  fullScreen.enterFullscreen('bodyTag')
});

onClick('mode-measuring', function() {
  gps.toggleMeasuring()
  let ele = document.getElementById('mode-measuring')
  if (gps.getMeasuringStatus() > -1)
    ele.className='active'
  else
    ele.className=''
});

onClick('mode-keep-center', function() {
  gps.toggleKeepCenter()
  let ele = document.getElementById('mode-keep-center')
  if (gps.getKeepCenterStatus())
    ele.className='active'
  else
    ele.className=''
});
